body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  color: #f5086c;
  padding-top: 30px;
}

.head {
  font-size: 24px;
  padding: 20px 0 20px 10%;
  font-weight: 700;
}

nav a {
  color: #696963;
  padding: 8px 1% 8px 1%;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
}

.navlink {
  padding: 0 0 0 30%;
}

.blog {
  color: white;
  background: #f1356d;
  border-radius: 8px;
}

.App {
  padding-left: 10%;
}

h1 {
  padding: 20px 0 0 0;
  font-size: 18px;
  font-weight: 500;
}

.blogpreview {
  border-bottom: 1px solid orchid;
  padding-left: 10px;
  padding-right: 50px;
  margin-right: 50px;
}

.blogpreview h2 { 
  color: rgb(235, 17, 126);
}

.blogpreview:hover {
  box-shadow: 1px 3px 5px grey;
}

.author {
  padding-top: 40px;
  color: red;
}

.delete {
  padding: 4px;
  color: white;
  background: rgb(235, 17, 126);
  margin-bottom: 10px;
  border-radius: 8px;
  border-color: red;
} 

.delete:hover {
  box-shadow: 1px 3px 5px grey;
}

.loading {
  padding-top: 100px;
  color: rgb(235, 17, 126);
  font-size: 36px;
}

.error {
  font-size: 36px;
  color: grey;
  font-weight: 500;
  padding: 200px 0 0 10%;
}

.blogpreview a {
  text-decoration: none;
  color: black;
}

.newblog {
  max-width: 400px;
  margin: 0 auto;
  margin-left: 300px;
}

@media only screen and (max-width: 600px) {
  .newblog {
    max-width: 350px;
    margin: 0;
  }
  .newblog h1 {
    font-size: 24px !important;
  }
  .newblog label {
    font-size: 16px !important;
  }
}

.newblog h1 {
  color: rgb(235, 17, 126);
  font-size: 36px;
}
.newblog label {
  text-align: left;
  display: block;
  color: grey;
  font-size: 24px;
  font-weight: 500;
}

.newblog input, .newblog textarea, .newblog select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block; 
}

.newblog button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer; 
  margin-left: 40%; 
}

